	module.exports = {"data":{"challengeNode":{"title":"Problem 299: Three similar triangles","description":["Four points with integer coordinates are selected:A(a, 0), B(b, 0), C(0, c) and D(0, d), ","with 0 < a < b and 0 < c < d.","Point P, also with integer coordinates, is chosen on the line AC so that the three triangles ABP, CDP and BDP are all similar.","","It is easy to prove that the three triangles can be similar, only if a=c.","","So, given that a=c, we are looking for triplets (a,b,d) such that at least one point P (with integer coordinates) exists on AC, making the three triangles ABP, CDP and BDP all similar.","","For example, if (a,b,d)=(2,3,4), it can be easily verified that point P(1,1) satisfies the above condition. ","Note that the triplets (2,3,4) and (2,4,3) are considered as distinct, although point P(1,1) is common for both.","","If b+d < 100, there are 92 distinct triplets (a,b,d) such that point P exists.","If b+d < 100 000, there are 320471 distinct triplets (a,b,d) such that point P exists.","If b+d < 100 000 000, how many distinct triplets (a,b,d) are there such that point P exists?"],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-299-three-similar-triangles","blockName":"Project Euler","tests":[{"text":"<code>euler299()</code> should return 549936643.","testString":"assert.strictEqual(euler299(), 549936643, '<code>euler299()</code> should return 549936643.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler299() {\n  // Good luck!\n  return true;\n}\n\neuler299();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-300-protein-folding","id":"5900f4971000cf542c50ffaa"},"slug":"/coding-interview-prep/project-euler/problem-299-three-similar-triangles"}}

